<template>

  <b>{{ msg }}</b>
  <p>IBM Java Full Stack -</p>
  <p>Yan.Ru.Li@ibm.com</p>
  <p> CDN </p>
  <p>
    <a href="https://frontend.022001.xyz" target="_blank">
      https://frontend.022001.xyz
    </a>
  </p>
  <br>
  <hr>
  <div id="myCanvasContainer">
    <canvas width="300" height="300" id="myCanvas" ref="canvas">
      <p></p>
    </canvas>
  </div>
  <div id="tags">
    <ul>
      <li v-for="(tag, index) in members" :key="index">
        <a href="#" style="height: auto;font-size: 40px;color: #fff" onclick="return false">
          {{ tag.name }}
        </a>
      </li>
    </ul>
  </div>
</template>

<script>

import CloudTags from '../assets/js/tagcanvas.min.js'
import Data from '../mock/data.js'

export default {
  name: "HomeView",
  props: {
    msg: String
  },
  data() {
    return {
      members: []
    }
  },
  methods: {
    init() {
      try {
        TagCanvas.Start('myCanvas', 'tags', {
          // textColour: null,
          //
          // decel: 0.8,
          // textHeight: 14,
          // minSpeed: 0.07,
          // initial: [
          //   0.1 * Math.random() + 0.01,
          //   -(0.1 * Math.random() + 0.01),
          // ],
          textFont: 'Impact,"Arial Black",sans-serif',
          textColour: '#6DD5FA',
          dragControl: 1,
          depth: 0.8,
          maxSpeed: 0.09,
          minSpeed: 0.07,
          shape: "hcylinder",
          lock: "x",
          initial: [0, 0.1],
          shuffleTags: true,
          textHeight: 20
        });
      } catch (e) {
        // something went wrong, hide the canvas container
        document.getElementById('myCanvasContainer').style.display =
            'none';
      }
    },
  }, mounted() {

    const tagsData = Data.songData.songInfo
    let tagsCloud = [];
    for (let index in tagsData) {
      let tagText = {};
      let tagEmo = {};
      tagText.name = tagsData[index].name;
      tagEmo.name = tagsData[index].power;
      tagsCloud.push(tagText);
      tagsCloud.push(tagEmo);
    }
    // console.log(tagsCloud)
    this.members = tagsCloud

    let canvas = this.$refs.canvas;
    // canvas.width = document.body.offsetWidth;
    // canvas.height = document.body.offsetHeight;
    canvas.width = 700;
    canvas.height = 500;

    this.$nextTick(function () {
      this.init();
    });
    document.body.addEventListener('keyup', this.onKeyup)
  }

}
</script>

